.data-list {
  &__main {
    justify-content: center;
    margin-bottom: 0;
  }

  &__item {
    padding: 32px 0;
  }

  &__label {
    margin-bottom: 12px;
    font-size:14px;
    line-height:1;
  }

  &__num {
    font-size:30px;
    font-weight:bold;
    color:#2661E9;
    line-height:1;
  }
}

@media (max-width: 991px) {
  .data-list {
    &__item {
      text-align: center;
    }
  }
}
